/* ==================== GLOBAL STYLES ==================== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 50%, #0a0a0a 100%);
  color: #e0e0e0;
  min-height: 100vh;
  overflow-x: hidden;
}

/* ==================== LAYOUT ==================== */
.Home {
  margin-left: 85px;
  width: calc(100% - 85px);
  min-height: 100vh;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.home-link {
  display: inline;
  text-decoration: none;
  color: inherit;
  background: transparent;
  border: none;
}

/* ==================== SIDEBAR ==================== */
.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  width: 85px;
  height: 100vh;
  background: rgba(10, 10, 10, 0.95);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-right: 1px solid rgba(255, 255, 255, 0.08);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
  z-index: 1000;
}

.sidebar-logo {
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  width: 100%;
  display: flex;
  justify-content: center;
}

.sidebar-logo .logo-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
}

.sidebar-logo img {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  transition: transform 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.sidebar-logo img:hover {
  transform: scale(1.1);
}

.sidebar-nav {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 0;
  width: 100%;
}

.nav-item {
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.nav-item:last-child {
  border-bottom: none;
}

.nav-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 18px 10px;
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  font-size: 9px;
  font-weight: 600;
  letter-spacing: 1.2px;
  font-family: 'JetBrains Mono', monospace;
  transition: all 0.25s ease;
  background: transparent;
  border: none;
  cursor: pointer;
  width: 100%;
  text-transform: uppercase;
}

.nav-link i {
  font-size: 20px;
  transition: all 0.25s ease;
}

.nav-link:hover {
  color: #fff;
  background: rgba(255, 255, 255, 0.05);
}

.nav-link:hover i {
  transform: scale(1.1);
}

/* ==================== HERO SECTION ==================== */
.hero {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 60px 80px;
  gap: 30px;
  text-align: left;
  min-height: auto;
  background: transparent;
}

.hero-text {
  max-width: 700px;
  order: 2;
}

.hero-text h1 {
  font-size: 42px;
  font-weight: 700;
  line-height: 1.2;
  color: #fff;
  margin-bottom: 10px;
  font-family: 'JetBrains Mono', monospace;
}

.highlight,
.text {
  color: #fff;
  font-weight: 700;
  background: none;
  -webkit-text-fill-color: #fff;
}

.highlight1 {
  color: rgba(255, 255, 255, 0.5);
  font-weight: 400;
  background: none;
  -webkit-text-fill-color: rgba(255, 255, 255, 0.5);
  font-style: italic;
}

.role {
  margin-top: 8px;
  font-size: 20px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  font-family: 'JetBrains Mono', monospace;
  background: none;
  -webkit-text-fill-color: rgba(255, 255, 255, 0.7);
}

.rotating-text {
  font-size: 20px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  height: 30px;
  display: flex;
  align-items: center;
  font-family: 'JetBrains Mono', monospace;
}

.rotating-text .text {
  border-right: 2px solid rgba(255, 255, 255, 0.7);
  padding-right: 5px;
  white-space: nowrap;
  color: rgba(255, 255, 255, 0.7);
  background: none;
  -webkit-text-fill-color: rgba(255, 255, 255, 0.7);
}

.intro-text {
  color: rgba(255, 255, 255, 0.6);
  margin-top: 20px;
  font-size: 15px;
  line-height: 1.8;
  max-width: 550px;
}

/* Quick Links Section */
.quick-links {
  margin-top: 30px;
  margin-bottom: 25px;
}

.quick-links h3 {
  font-size: 18px;
  font-weight: 600;
  color: #fff;
  margin-bottom: 15px;
  font-family: 'JetBrains Mono', monospace;
}

.quick-links ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.quick-links li {
  display: flex;
  align-items: center;
}

.quick-links li::before {
  content: "â€¢";
  color: rgba(255, 255, 255, 0.4);
  margin-right: 10px;
  font-size: 12px;
}

.quick-links a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: underline;
  text-underline-offset: 3px;
  font-size: 14px;
  transition: color 0.2s ease;
  display: flex;
  align-items: center;
  gap: 8px;
}

.quick-links a:hover {
  color: #fff;
}

.quick-links a i {
  font-size: 14px;
  width: 18px;
}

/* Hero Image */
.hero-img {
  order: 1;
  width: 160px;
  height: 160px;
  border-radius: 50%;
  overflow: hidden;
  border: 3px solid rgba(255, 255, 255, 0.15);
  flex-shrink: 0;
  background: rgba(255, 255, 255, 0.05);
}

.hero-img2 {
  border-radius: 12px;
  overflow: hidden;
  border: 2px solid rgba(255, 255, 255, 0.1);
  width: auto;
  height: auto;
  max-width: 180px;
  background: rgba(255, 255, 255, 0.05);
}

.hero-img img,
.hero-img2 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Buttons */
.hero-buttons {
  margin-top: 10px;
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.btn,
.btn-outline {
  padding: 12px 24px;
  font-size: 13px;
  font-weight: 600;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'JetBrains Mono', monospace;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.btn {
  background: rgba(255, 255, 255, 0.08);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.15);
}

.btn:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
}

.btn-outline {
  background: transparent;
  color: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-outline:hover {
  background: rgba(255, 255, 255, 0.08);
  color: #fff;
}

.btn a,
.btn-outline a {
  text-decoration: none;
  color: inherit;
  display: flex;
  align-items: center;
  gap: 8px;
}

.btn i,
.btn-outline i {
  font-size: 12px;
}

.line {
  display: none;
}

/* ==================== ABOUT SECTION ==================== */
.about-us {
  padding: 60px 80px;
  background: rgba(255, 255, 255, 0.02);
  border-top: 1px solid rgba(255, 255, 255, 0.06);
  color: #fff;
  max-width: none;
  margin: 0;
  border-radius: 0;
  text-align: left;
  display: flex;
  flex-direction: column;
}

.about-us h2 {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 25px;
  color: #fff;
  font-family: 'JetBrains Mono', monospace;
  background: none;
  -webkit-text-fill-color: #fff;
}

.about-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 25px;
}

.code-block {
  font-family: 'Inter', sans-serif;
  background: rgba(255, 255, 255, 0.03);
  color: rgba(255, 255, 255, 0.8);
  padding: 25px;
  border-radius: 10px;
  line-height: 1.8;
  border: 1px solid rgba(255, 255, 255, 0.08);
  font-size: 14px;
  max-width: 600px;
}

.code-block p {
  margin-bottom: 12px;
}

.code-block p:last-child {
  margin-bottom: 0;
}

/* ==================== PROJECTS SECTION ==================== */
.projects,
.projects-bug {
  margin: 0;
  padding: 50px 80px;
  text-align: left;
  border: none;
  border-radius: 0;
  background: transparent;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  max-width: none;
}

.projects p,
.projects-bug p {
  font-size: 15px;
  line-height: 1.7;
  margin-bottom: 20px;
  color: rgba(255, 255, 255, 0.7);
  max-width: 550px;
}

.projects .btn,
.projects-bug .btn {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.15);
  color: #fff;
  padding: 10px 20px;
  font-size: 12px;
}

/* ==================== PHOTO GALLERY ==================== */
.others {
  margin: 0;
  padding: 50px 80px;
  width: 100%;
  max-width: none;
  text-align: left;
  background: rgba(255, 255, 255, 0.01);
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.photo-title {
  font-size: 24px;
  margin-bottom: 25px;
  color: #fff;
  font-weight: 700;
  font-family: 'JetBrains Mono', monospace;
  background: none;
  -webkit-text-fill-color: #fff;
}

.photo-title::after {
  display: none;
}

.photo-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.photos {
  display: flex;
  gap: 15px;
  overflow-x: auto;
  padding: 15px;
  border-radius: 10px;
  scroll-behavior: smooth;
  background: rgba(255, 255, 255, 0.02);
  width: 100%;
  border: 1px solid rgba(255, 255, 255, 0.06);
}

.photos::-webkit-scrollbar {
  height: 6px;
}

.photos::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.02);
  border-radius: 10px;
}

.photos::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.15);
  border-radius: 10px;
}

.photos img {
  min-width: 220px;
  max-width: 260px;
  height: 260px;
  object-fit: cover;
  border-radius: 10px;
  border: 2px solid rgba(255, 255, 255, 0.08);
  transition: all 0.3s ease;
}

.photos img:hover {
  border-color: rgba(255, 255, 255, 0.2);
}

.scroll-btn {
  position: absolute;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.15);
  cursor: pointer;
  background: rgba(10, 10, 10, 0.9);
  color: rgba(255, 255, 255, 0.7);
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
  z-index: 10;
}

.scroll-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
}

.scroll-btn.left {
  left: -20px;
}

.scroll-btn.right {
  right: -20px;
}

/* ==================== MAP ==================== */
.map {
  padding: 30px 80px;
  background: transparent;
}

.map iframe {
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.08);
}

/* ==================== FOOTER ==================== */
.footer {
  margin: 0;
  padding: 60px 80px;
  background: rgba(255, 255, 255, 0.02);
  border-top: 1px solid rgba(255, 255, 255, 0.06);
  border-radius: 0;
  text-align: left;
  color: #fff;
}

.footer-title {
  font-size: 24px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 12px;
  font-family: 'JetBrains Mono', monospace;
}

.footer-text {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.6);
  max-width: 450px;
  margin-bottom: 25px;
  line-height: 1.7;
}

.footer-links {
  margin-top: 20px;
  display: flex;
  gap: 18px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.footer-link {
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  text-decoration: underline;
  text-underline-offset: 3px;
  transition: color 0.2s ease;
}

.footer-link:hover {
  color: #fff;
}

.Rules {
  margin-top: 15px;
  display: flex;
  gap: 12px;
  align-items: center;
}

.Rules .footer-link {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.4);
}

.footer-bottom {
  font-size: 12px;
  margin-top: 25px;
  color: rgba(255, 255, 255, 0.35);
}

.footer-contact {
  max-width: 380px;
  padding: 0;
  background: transparent;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.footer-input,
.footer-textarea {
  width: 100%;
  padding: 12px 14px;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.03);
  color: #fff;
  font-size: 13px;
  font-family: 'Inter', sans-serif;
  transition: all 0.3s ease;
  outline: none;
}

.footer-input::placeholder,
.footer-textarea::placeholder {
  color: rgba(255, 255, 255, 0.35);
}

.footer-input:focus,
.footer-textarea:focus {
  border-color: rgba(255, 255, 255, 0.3);
  background: rgba(255, 255, 255, 0.05);
}

.footer-btn {
  background: rgba(255, 255, 255, 0.08);
  color: #fff;
  padding: 12px 24px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 6px;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  font-family: 'JetBrains Mono', monospace;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
  width: fit-content;
}

.footer-btn:hover {
  background: rgba(255, 255, 255, 0.15);
}

/* ==================== POLICY PAGES ==================== */
.policy-page {
  padding: 60px 80px;
  max-width: none;
  margin: 0;
  color: #fff;
  background: transparent;
  margin-left: 85px;
  min-height: 100vh;
}

.policy-page h1 {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 25px;
  color: #fff;
  font-family: 'JetBrains Mono', monospace;
}

.policy-subhead {
  margin-top: 25px;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 600;
}

.policy-list {
  line-height: 1.7;
  margin-top: 15px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.65);
  padding-left: 18px;
}

.policy-list li {
  margin-bottom: 10px;
}

.policy-list a {
  color: rgba(255, 255, 255, 0.85);
  text-decoration: underline;
}

/* ==================== ANIMATIONS ==================== */
@keyframes typing {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

@keyframes blink {

  0%,
  100% {
    border-color: rgba(255, 255, 255, 0.7);
  }

  50% {
    border-color: transparent;
  }
}

.typing {
  animation: typing 1.5s steps(30), blink 1s infinite step-end;
}

/* ==================== RESPONSIVE ==================== */
@media (max-width: 900px) {
  .sidebar {
    position: fixed;
    bottom: 0;
    left: 0;
    top: auto;
    width: 100%;
    height: 65px;
    flex-direction: row;
    justify-content: space-around;
    padding: 8px 0;
    border-right: none;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
  }

  .sidebar-logo {
    display: none;
  }

  .sidebar-nav {
    flex-direction: row;
    gap: 0;
    width: auto;
  }

  .nav-item {
    border-bottom: none;
  }

  .nav-link {
    padding: 8px 12px;
    font-size: 7px;
  }

  .nav-link i {
    font-size: 16px;
  }

  .Home {
    margin-left: 0;
    width: 100%;
    padding-bottom: 75px;
  }

  .hero,
  .about-us,
  .projects,
  .projects-bug,
  .others,
  .footer,
  .map {
    padding-left: 30px;
    padding-right: 30px;
  }

  .hero {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .hero-text h1 {
    font-size: 32px;
  }

  .policy-page {
    margin-left: 0;
    padding: 50px 30px;
    padding-bottom: 90px;
  }
}

@media (max-width: 600px) {
  .hero-text h1 {
    font-size: 26px;
  }

  .role,
  .rotating-text {
    font-size: 16px;
  }

  .hero-img {
    width: 120px;
    height: 120px;
  }

  .photos img {
    min-width: 180px;
    max-width: 220px;
    height: 220px;
  }

  .nav-link span {
    display: none;
  }

  .about-us h2,
  .footer-title,
  .photo-title {
    font-size: 20px;
  }

  .code-block {
    padding: 18px;
    font-size: 13px;
  }

  .quick-links h3 {
    font-size: 16px;
  }

  .quick-links a {
    font-size: 13px;
  }
}